.vis-grid-view {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.react-grid-item > .react-resizable-handle.react-resizable-handle-se {
  bottom: 0;
  right: 0;
  cursor: se-resize;
  opacity: 0;
}
.react-grid-item:hover > .react-resizable-handle.react-resizable-handle-se {
  opacity: 1;
}

